.App {
  text-align: center;
  min-height: 100vh;
  background-color: #f5f5f5;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

button {
  cursor: pointer;
}

button:disabled {
  cursor: not-allowed;
  opacity: 0.7;
}

input, button {
  font-family: inherit;
}

.search-container {
    padding: 0 20px;
}
.search-box {
    position: relative;
}
.search-input {
    width: 100%;
    padding: 8px 12px;
    font-size: 12px;
    border: 2px solid #e0e0e0;
    border-radius: 20px;
    transition: border-color 0.3s;
}
.search-input:focus {
    outline: none;
    border-color: #1976d2;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
.main-content {
  border-radius: 8px;
  background-color: white;
  padding-top: 82px;
}
.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;  
  padding: 5px 5px; 
  top: 85px;
  right: 20px;
  left: 270px; 
}

.page-header h1,h2 {  
  margin: 0;
  padding-left: inherit;
  color: rgb(76, 175, 80);
  font-size: x-large!important;
  font-weight: 500!important;
}
.breadcrumbs {
  margin: 10px 0;
  padding-left: 20px;
  list-style: none;
  display: flex;
  align-items: center;
  font-size: 17px;
  flex-wrap: wrap;
}
.breadcrumbs-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
} 
.breadcrumbs ul {
  display: flex;
  gap: 5px;
  padding: 0;
  margin: 0;
}

.breadcrumbs li {
  display: flex;
  align-items: center;
}
.breadcrumbs a {
  text-decoration: none;
  color: #777;
  transition: color 0.25s ease;
}
.breadcrumbs a:hover {
  color: #555;
}
.breadcrumbs span {
  color: black;
  font-weight: 600;
}
.breadcrumbs li a {
  text-decoration: none;
  color: #777;
  transition: color 0.25s ease;
}

.breadcrumbs li a:hover {
  color: #555;
}

.breadcrumbs li span {
  color: #333;
}

.breadcrumbs li:not(:last-child)::after {
  content: '>';
  margin: 0 5px;
  color: #6c757d;
}
.row {
  display: flex;
}
.col-1 {
  float: left;
  flex: 0 0 8.333333%;
}

.col-2 {
  float: left;
  flex: 0 0 16.666667%;
}

.col-3 {
  float: left;
  flex: 0 0 25%;
}
.col-4 {
  float: left;
  flex: 0 0 33.333333%;
}

.col-5 {
  float: left;
  flex: 0 0 41.666667%;
}
.col-6 {
  float: left;
  flex: 0 0 50%;
}
.col-7 {
  float: left;
  flex: 0 0 58.333333%;
}
.col-8 {
  float: left;
  flex: 0 0 66.666667%;
}
.col-9 {
  float: left;
  flex: 0 0 75%;
}
.col-10 {
  float: left;
  flex: 0 0 83.333333%;
}
.col-11 {
  float: left;
  flex: 0 0 91.666667%;
}
.col-12 {
  float: left;
  flex: 0 0 100%;
}
.left, .right {
  width: 25%;
}

.middle {
  width: 50%;
}
.form-group {
  display: flex;
  margin-bottom: 1.5rem;
  align-items: flex-start;
}
.form-group label {
  min-width: 150px;
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: black;
}

.justifyLabel{
  text-align: justify;
}
.form-group input[type="text"],input[type="email"], input[type="password"], input[type="number"], input[type="date"], input[type="time"] {
  min-width: 300px;
  border-radius: 20px;
  border: 1px solid #ddd;
  /* flex: 1 1; */
  padding: 0.5rem;
}
.form-group input[type="checkbox"] {
  min-width: 24px!important;
  max-height: 24px!important;
}
.form-group textarea {
  min-width: 75%;
  min-height: 250px;
  border: 1px solid #ddd;
}
.form-group select { 
  border-radius: 8px;
  border: 1px solid #ddd; 
  padding: 0.5rem;
}
.error-message {
  color: #dc3545;
  padding: 10px 20px;
  margin-bottom: 10px;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 4px;
  font-size: 14px;
}

/* Global button styles */
.submit-button{
    background-color: #1976d2!important;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 500;
    transition: background-color 0.3s ease;
    width: auto; 
}

.submit-button:hover {
    background-color: #42A5F5;
}

.submit-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}
.cancel-button {
  background-color: #d32f2f!important;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  transition: background-color 0.3s ease;
  width: auto; 
}
.cancel-button {
  background-color: #d32f2f;
}

.cancel-button:hover {
  background-color: #b71c1c;
}

.image-container {
  overflow: hidden;
  display: inline-block;
}

.image {
  transition: transform 0.3s ease-in-out; /* Animasyon iin */
}

.image-container:hover .image {
  transform: scale(1.2); /* Bytme oran */
}

/* DataGrid zelletirmeleri */
.data-grid-container {
  padding: 10px 10px 0px 5px;
  height: calc(100vh - 250px);
  min-height: 400px;
}

.data-grid-container .MuiDataGrid-root {
  border-radius: 8px;
}

.data-grid-container .MuiDataGrid-columnHeaders {
  background-color: #f5f5f5;
  border-bottom: 1px solid #e0e0e0;
}

.data-grid-container .MuiDataGrid-cell {
  border-bottom: 1px solid #f0f0f0;
}

.data-grid-container .MuiDataGrid-row:hover {
  background-color: #f8f9fa;
}

.css-t89xny-MuiDataGrid-columnHeaderTitle{
  font-size: medium!important; /* Balk yaz boyutu */
  font-weight: 600!important; /* Balk kalnl */
  color: slategrey!important /* Balk rengi */ 
}

@media (max-width: 768px) {  
  .main-content {
    padding-top: 0;
  }
}

